$pretty--color-info: #2c2c2c;
@import "~pretty-checkbox/src/pretty-checkbox.scss";

* {
  box-sizing: border-box;
}

.container {
  max-width: 415px;
  margin: auto;
  padding: 1rem 1.5rem;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  border: 1px solid lightgrey;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  flex-grow: 1; /* make the content container fill the entire view */
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}

.save-button {
  border: 1px solid black;
  padding: 0.5rem 2rem;
  background-color: white;
}

.indentation {
  margin: 1rem 0 1rem 1rem;
}

.mx {
  margin: 0.25rem 0;
}
